.card-container{
    width:300px;
    overflow: hidden;
    box-shadow: 0px 0px 15px -5px;
    transition:0.3s;
    animation: ease-in-out;
}

.image-container img{
    overflow: hidden;
    height:200px;

}
.card-container:hover{
    transform: scale(1.1);
    box-shadow: 0px 0px 15px -5px;

}
.card-content{
    margin:1rem;
    margin-top: 0.5rem;

}
h3,p{
    margin:0;
    padding-bottom: 10px;

}
.card-title{
    margin-bottom:0.5rem;
    justify-content: center;

}
.btn button{
    padding:0.5rem;
    background-color: white;
    transition: 0.2s;
    margin-bottom: 0.5rem;

}
.btn button:hover{
    background: rgba(27,156,252,0.1);
}

a{
    color:black;
    text-decoration: none;
    font-weight: bold;

}